import React, { useEffect, useState } from "react";
import { Form, Button, Card, Col, Row } from "react-bootstrap";
import axios from "axios";
import { Checkbox } from "../checkbox/Checkbox";
import { CheckboxGroup } from "../checkbox/CheckboxGroup";
import dayjs from "dayjs";

function AttendanceCheck() {
  const [members, setMembers] = useState("");
  const [memberIds, setMemberIds] = useState([]);

  const [switchChecked, setSwitchChecked] = useState(false);
  const [selectYear, setSelectYear] = useState();
  const [selectMonth, setSelectMonth] = useState();
  const [selectDay, setSelectDay] = useState();

  useEffect(() => {
    axios
      .get("http://localhost:8080/api/attendances/check")
      .then((response) => {
        setMembers(response.data);
        console.log("---");
        console.log(response.data);
      })
      .catch((error) => console.log(error));

  }, []);

  const handleSubmit = (event) => {
    event.preventDefault(); //  .
    memberIds.sort();

    const year = parseInt(selectYear);
    const month = parseInt(selectMonth) - 1;
    const day = parseInt(selectDay);
    const localDate = new Date(year, month, day);
    //Spring  LocalDateTime  JSON   
    //      .
    // T    .
    let attendanceDate = dayjs(localDate).format("YYYY-MM-DDTHH:mm:ss");
    let formData;

    if (switchChecked === true) {
      formData = {
        memberIds: memberIds,
        attendanceDate: attendanceDate
      };
    } else {
      //  switch  false    ,,  null 
      formData = {
        memberIds: memberIds,
        attendanceDate: null
      };
    }

    axios
      .post("http://localhost:8080/api/attendances/create", formData, {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((response) => {
        console.log(response);
        alert("  .");
        window.location.replace("/attendances");
      })
      .catch(function (error) {
        console.log(error);
        alert("  .");
      });
  };

  const handleSwitchChange = () => {
    setSwitchChecked(!switchChecked);
  };

  const handleSelectYearChange = (e) => {
    setSelectYear(e.target.value);
  };
  const handleSelectMonthChange = (e) => {
    setSelectMonth(e.target.value);
  };
  const handleSelectDayChange = (e) => {
    setSelectDay(e.target.value);
  };

  //  ,  (   12 )
  const currentYear = new Date().getFullYear();

  //Date  0     .
  const daysInYearAndMonth = new Date(selectYear, selectMonth, 0).getDate();

  // , ,   
  const years = Array.from({ length: 5 }, (_, i) => currentYear - i);
  const months = Array.from({ length: 12 }, (_, i) => i + 1);
  const days = Array.from({ length: daysInYearAndMonth }, (_, i) => i + 1);

  return (
    <div className="container">
      <p> </p>

      <Form onSubmit={handleSubmit}>
        <Card>
          <Card.Body>
            <p>  </p>
            <Form.Group>
              <Form.Check
                type="switch"
                id="custom-switch"
                label={
                  <span>
                     
                    <small>
                      {" "}
                      -        
                      .
                    </small>
                  </span>
                }
                checked={switchChecked}
                onChange={handleSwitchChange}
              />
            </Form.Group>
            <Row>
              <Form.Group as={Col} className="selectMenu mt-2">
                <Form.Select
                  value={selectYear}
                  onChange={handleSelectYearChange}
                  disabled={!switchChecked}
                >
                  <option value=""></option>
                  {years.map((year) => (
                    <option key={year} value={year}>
                      {year}
                    </option>
                  ))}
                </Form.Select>
              </Form.Group>
              <Form.Group as={Col} className="selectMenu mt-2">
                <Form.Select
                  value={selectMonth}
                  onChange={handleSelectMonthChange}
                  disabled={!switchChecked}
                >
                  <option value=""></option>
                  {months.map((month) => (
                    <option key={month} value={month}>
                      {month}
                    </option>
                  ))}
                </Form.Select>
              </Form.Group>
              <Form.Group as={Col} className="selectMenu mt-2">
                <Form.Select
                  value={selectDay}
                  onChange={handleSelectDayChange}
                  disabled={!switchChecked}
                >
                  <option value=""></option>
                  {days.map((day) => (
                    <option key={day} value={day}>
                      {day}
                    </option>
                  ))}
                </Form.Select>
              </Form.Group>
            </Row>
          </Card.Body>
        </Card>

        <CheckboxGroup
          label=" "
          values={memberIds}
          onChange={setMemberIds}
        >
          {members
            ? members.map((a, i) => (
                <div key={i}>
                  <Card className="mb-3">
                    <Card.Header>
                      <p>{a.groupName}</p>
                    </Card.Header>
                    <Card.Body>
                      {a.members
                        ? a.members.map((b, j) => (
                            // <Form.Check key={j} inline className="memberClass" type="checkbox" id={b.memberId} label={b.name} />
                            <Checkbox key={j} value={b.memberId}>
                              {b.name}
                            </Checkbox>
                          ))
                        : null}
                    </Card.Body>
                  </Card>
                </div>
              ))
            : null}
        </CheckboxGroup>

        <Button
          variant="primary"
          type="submit"
          className="btnSave"
          disabled={memberIds.length === 0}
        >
           
        </Button>
      </Form>
    </div>
  );
}

export default AttendanceCheck;
